/**
 *       :focus
 *        tab.
 *         -   .clicked
 *
 */
export class ButtonHoverTab {
    /**
     *  .
     *
     * @param {HTMLButtonElement} button  .
     *
     */
    constructor(btn) {
        this.btn = btn;
        this.bindEventListeners();
    }

    bindEventListeners() {
        this.btn.addEventListener("mousedown", () => {
            if(!this.btn.classList.contains("clicked")) {
                this.btn.classList.add("clicked");
            }
        });

        this.btn.addEventListener("blur", () => {
            if(this.btn.classList.contains("clicked")) {
                this.btn.classList.remove("clicked");
            }
        });
    }
}

document.querySelectorAll("button").forEach((el) => new ButtonHoverTab(el));
