/**    . */
export class Input {
    /**
     *    .
     *
     * @param {object} props  .
     * @param {HTMLElement} props.container  .
     * @param {HTMLInputElement} props.input  .
     */
    constructor({container, input}) {
        this.el = container;
        this.input = input;
        this.updateIsFilledStatus();
        this.bindEventListeners();
    }

    /**
     *      .
     *
     * @returns {void}
     */
    bindEventListeners() {
        this.input.addEventListener("input", this.onInput.bind(this));
        this.input.addEventListener("beforeinput", this.onBeforeInput.bind(this));
    }

    /**
     *   .
     *
     * @returns {string}  .
     */
    getValue() {
        return this.input.value;
    }

    /**
     *   .
     *
     * @returns {HTMLElement}   .
     */
    hasLabel() {
        return this.el.querySelector(".form-field__label");
    }

    /**
     *   ,  ,     ,  .
     *
     * @returns {void}
     */
    updateIsFilledStatus() {
        if (this.getValue() && this.hasLabel()) {
            this.el.classList.add("filled");
        } else {
            this.el.classList.remove("filled");
        }
    }

    /**
     *    'input'.
     *
     * @returns {void}
     */
    onInput() {
        this.updateIsFilledStatus();
        if (this.input.maxLength > 0 && this.input.maxLength < this.input.value.length) {
            this.input.value = this.input.value.slice(0, this.input.maxLength);
        }
    }

    /**
     *      -  ,   .
     *
     * @param {Event} event ,     .
     * @returns {void}
     */
    onBeforeInput(event) {
        if (this.input.pattern && event.data) {
            const pattern = new RegExp(this.input.pattern);
            if (!pattern.test(event.data)) {
                event.preventDefault();
            }
        }
    }

    /**
     *       .
     *
     * @param {string} str   .
     * @returns {string} .
     */
    replaceBadCharacters(str) {
        let last = "";
        const match = str.match(this.input.pattern);

        if (match || str === "") {
            last = match ? match[0] : "";
        }
        return last;
    }
}
