import {Input} from "@main/components/form-field/input";
import {replaceBadInNumber} from "@main/utils/numberFormat";

/**     . */
export class NumberInput extends Input {
    /**
     *  .
     *
     * @param {object} props  .
     * @param {HTMLElement} props.container  .
     * @param {HTMLInputElement} props.input  .
     */
    constructor(props) {
        super(props);
    }

    /**
     *      .
     *
     * @returns {void}
     */
    bindEventListeners() {
        super.bindEventListeners();
        this.el.addEventListener("paste", this.onPaste.bind(this));
    }

    /**
     *      .
     *
     * @param {Event} e ,     .
     * @returns {void}
     */
    onInput(e) {
        super.onInput();
        const input = e.target;
        this.replaceBadCharacters(input);
    }

    /**
     *  ,      ,    .
     *
     * @param {HTMLInputElement} input  .
     * @returns {void}
     */
    replaceBadCharacters(input) {
        input.value = replaceBadInNumber(input.value);
    }

    /**
     *        .
     *
     * @param {Event} e ,     .
     * @returns {void}
     */
    onPaste(e) {
        const input = e.target;
        this.replaceBadCharacters(input);
    }
}
