import {Input} from "@main/components/form-field/input";
import {getFormattedFileSize} from "@main/utils/file-size-format";

/**   . */
export class FileInput extends Input {
    /**
     *    .
     *
     * @param {object} props  .
     * @param {HTMLElement} props.container  .
     * @param {HTMLInputElement} props.input  .
     */
    constructor(props) {
        super(props);
        this.findElements();
    }

    /**
     *  .
     *
     * @returns {void}
     */
    findElements() {
        this.fileNameCaptionNode = this.el.querySelector(".js-form-field-file-caption");
    }

    /**
     *  .
     *
     * @returns {void}
     */
    bindEventListeners() {
        super.bindEventListeners();
        this.input.addEventListener("change", this.onChange.bind(this));
    }

    /**
     * ,    .
     *
     * @returns {void}
     */
    onChange() {
        const [file] = this.input.files;
        this.fileNameCaptionNode.innerText = file ? `${file.name}, ${getFormattedFileSize(file.size)}` : "";
    }

    /**
     *   .
     *
     * @returns {File} .
     */
    getValue() {
        return this.input.files[0];
    }
}
