import vacancyItemTemplate from "@partials/vacancy-item.handlebars";

/**        . */
export default class ListItems {
    /**
     *  .
     *
     * @param {Element} list - DOM  .
     */
    constructor(list, items) {
        this.list = list;
        this.items = items;
        this.col = list.querySelector(".js-result-col");
    }

    /**
     *  .
     *
     * @returns {void}
     */
    clear() {
        this.col.innerHTML = "";
    }


    /**
     *     .
     *
     * @returns {void}
     */
    spreadItems() {
        this.items.forEach((item) => {
            this.col.insertAdjacentHTML("beforeend", vacancyItemTemplate({item: item}));
        });
    }
}
