import axios from "@main/axios";
import {EMAIL_REGEX, NUMBER_REGEX, PASSWORD_REGEX} from "@main/consts/regex";
import {VALID_FILE_TYPES} from "@main/consts/file-formats";
import {MAX_FILE_SIZE} from "@main/consts/file-size";
import {Modal} from "@main/components/modal";
import {ERROR_DEFAULT} from "@main/consts/messages";

/**      (, ). */
export class Form {
    static MAX_PHONE_LENGTH = 18;
    /**
     * @type {{Boolean}}   .
     * @private
     */
    result;

    /**
     *   .
     *
     * @param {HTMLFormElement} form  .
     */
    constructor(form) {
        this.el = form;
        this.url = this.el.dataset.url;
        this.findElements();
        this.bindEventListeners();
    }

    /**
     *   .
     *
     * @returns {void}
     */
    findElements() {
        this.fields = [...this.el.querySelectorAll(".js-form-field")].map((field) => ({
            node: field,
            input: field.querySelector(".js-form-field-input"),
            fileField: field.querySelector(".js-form-field-file-caption")
        }));
        this.errorModal = document.querySelector(".js-modal[data-modal-name='failed-modal']");
        this.errorModalText = this.errorModal.querySelector(".js-fail-modal-text");
        this.successModal = document.querySelector(".js-modal[data-modal-name='success-modal']");
    }

    /**
     *    .
     *
     * @param {HTMLDivElement} field   .
     * @returns {void}
     */
    hideFieldError(field) {
        field.classList.remove("error");
    }

    /**
     *   .
     *
     * @returns {void}
     */
    bindEventListeners() {
        this.el.addEventListener("submit", (e) => e.preventDefault());

        this.bindInputListeners();
    }

    /**
     *      .
     *
     * @returns {void}
     */
    bindInputListeners() {
        this.fields.forEach((field) => {
            field.input.addEventListener("input", () => this.hideFieldError(field.node));
            field.input.addEventListener("blur", () => this.checkInput(field));
            field.input.addEventListener("paste", () => this.hideFieldError(field.node));
        });
    }

    /**
     *  submit     modal.
     *
     * @param {Event} e ,    .
     * @param {HTMLElement} modal  .
     * @returns {void}
     */
    submitFormWithModal = async (e, modal) => {
        e.preventDefault();
        let modalResult;
        if (!this.isValid) {
            return
        }
        try {
            await Form.fetchForm(this.el, this.url);
            modalResult = this.successModal;
            this.clearForm();
        } catch (e) {
            modalResult = this.errorModal;
            if(e.response) {
                if (e.response.data?.message) {
                    this.errorModalText.innerHTML = e.response.data.message;
                } else {
                    this.errorModalText.innerHTML = ERROR_DEFAULT;
                }
            } else {
                this.errorModalText.innerHTML = ERROR_DEFAULT;
            }
        }
        Modal.close(modal);
        Modal.open(modalResult);
    }

    /**
     *   .
     *
     * @returns {void}
     */
    clearForm() {
        this.fields.forEach((field) => {
            field.input.value = "";
            field.node.classList.remove("filled");
            if (field.fileField) {
                field.fileField.innerHTML = "";
            }

            if (field.input.classList.contains("tomselected")) {
                field.input.tomselect.clear();
            }
        });
    }

    /**
     *     .
     *
     * @returns {boolean}    .
     */
    get isValid() {
        let isValid = true;
        this.fields.forEach((field) => {
            if(field.node.classList.contains("error")) {
                field.node.classList.remove("error");
            }
            if (!this.validateField(field.input)) {
                isValid = false;
                field.node.classList.add("error");
            }
        });

        return isValid;
    }

    /**
     *     .
     *
     * @param {object} field   .
     * @returns {void}
     */
    checkInput(field) {
        //    -     blur-e    required
        if (!field.input.value) {
            field.node.classList.remove("error");
        } else {
            if (!this.validateField(field.input)) {
                field.node.classList.add("error");
            } else {
                field.node.classList.remove("error");
            }
        }
    }

    /**
     *     .
     *
     * @param {HTMLInputElement} input   .
     * @returns {boolean}   .
     */
    isFilled(input) {
        if (input.required && !input.value) {
            return false;
        }
        return true;
    }

    /**
     *    .
     *
     * @param {HTMLInputElement} input   .
     * @returns {boolean}    .
     */
    validateField(input) {
        //    ,         .
        //        -   .
        //    - this.result = true
        //          this.result
        if(input.required) {
            if(input.pattern) {
                this.result = Form.isValidPattern(input) && Form.isValidLength(input);
            } else {
                //  true  ,  input          input-
                this.result = !!input.value;
            }
        } else if(!input.required) {
            this.result = true;
        }

        switch (input.type) {
            case "file":
                this.result = this.result && Form.isValidFile(input);
                break;

            case "tel":
                this.result = this.result && Form.isValidPhone(input);
                break;

            case "email":
                this.result = this.result && Form.isValidMail(input);
                break;

            case "number":
                this.result = this.result && Form.isValidNumber(input);
                break;

            case "password":
                this.result = this.result && Form.isValidPasswords(input) && Form.isValidPasswordByLetter(input) && Form.isValidPasswordByCapital(input);
                break;

            case "hidden":
                this.result = true;
                break;

            case "text":
                if(input.classList.contains("js-form-field-input-date")) {
                    this.result = this.result && Form.isValidDate(input);
                } else {
                    this.result = true;
                }
                break

            default:
                this.result = true;
                break;
        }
        return this.result;
    }

    /**
     *  .
     *
     * @param {HTMLFormElement} formNode DOM  .
     * @param {string} url Url  .
     * @param {number} id .
     * @returns {object}    ,  std.
     */
    static async fetchForm(formNode, url, id = undefined) {
        const data = new FormData(formNode);
        const {data: response} = await axios.post(url, data, {
            params: {
                id: id,
            },
        });
        return response;
    }

    /**
     *      .
     *
     * @param {HTMLInputElement} input   .
     * @returns {boolean}    .
     */
    static isValidPattern(input) {
        const regex = new RegExp(input.pattern);
        return regex.test(input.value);
    }
    /**
     *    .
     *
     * @param {HTMLInputElement} phoneInput  .
     * @returns {boolean}   .
     */
    static isValidPhone(phoneInput) {
        return phoneInput.value.length === Form.MAX_PHONE_LENGTH;
    }

    /**
     *    .
     *
     * @param {HTMLInputElement} mailInput  .
     * @returns {boolean}   .
     */
    static isValidMail(mailInput) {
        return EMAIL_REGEX.test(mailInput.value);
    }

    /**
     *     .
     *
     * @param {HTMLInputElement} numberInput   .
     * @returns {boolean}   .
     */
    static isValidNumber(numberInput) {
        if (numberInput.min && numberInput.max) {
            const min = +numberInput.min;
            const max = +numberInput.max;
            if (+numberInput.value < min || +numberInput.value > max) return;
        }
        return NUMBER_REGEX.test(numberInput.value);
    }

    /**
     *    .
     *
     * @param {HTMLInputElement} fileInput  .
     * @returns {boolean}   .
     */
    static isValidFile(fileInput) {
        let errors = [];
        let result = true;

        if(!fileInput.files.length) {
            //    - ,    -  true
            result = true;
        } else {
            //   ,     
            const file = fileInput.files[0];
            if (file.size > MAX_FILE_SIZE) {
                errors.push("      10.");
                result = false;
            }
            if(VALID_FILE_TYPES.indexOf(file.type) === -1) {
                errors.push("   : doc, docx, xls, xlsx, pdf, png, jpg.");
                result = false;
            }
        }

        if(errors.length > 0) {
            const errorMessage = errors.join("<br>");
            const inputErrorElement = fileInput.closest(".js-form-field").querySelector(".js-form-field-caption");
            inputErrorElement.innerHTML = errorMessage;
        }

        return result;
    }

    /**
     *    .
     *
     * @param {HTMLInputElement} input .
     * @returns {boolean}   .
     */
    static isValidLength(input) {
        if (input.maxLength && input.maxLength > 0) {
            if ( input.value.length > input.maxLength) {
                return false;
            }
        }

        if (input.minLength && input.minLength > 0) {
            if ( input.value.length < input.minLength) {
                return false;
            }
        }
        return true;
    }

    /**
     *         .
     *
     * @param {HTMLInputElement} passwordInput  .
     * @returns {boolean}      .
     */

    static isValidPasswordByLetter(passwordInput) {
        if(!(passwordInput.classList.contains("js-current-password"))) {
            const password = passwordInput.value;
            const errorField = passwordInput.closest(".js-form-field").querySelector(".js-validation-error");
            if(PASSWORD_REGEX.test(password)) {
                errorField.innerHTML ="";
            } else {
                errorField.innerHTML ="     8     ";
            }
            return PASSWORD_REGEX.test(password);
        } else {
            return true
        }
    }

    /**
     *      
     *
     * @param {HTMLInputElement} passwordInput  .
     * @returns {boolean}       
     */
    static isValidPasswordByCapital(passwordInput) {
        if(!(passwordInput.classList.contains("js-current-password"))) {
            const errorField = passwordInput.closest(".js-form-field").querySelector(".js-validation-error");
            const password = passwordInput.value;
            if(!(password === password.toLowerCase())) {
                errorField.innerHTML ="";
            } else {
                errorField.innerHTML ="     8     ";
            }
            return !(password === password.toLowerCase())
        } else {
            return true
        }

    }

    /**
     *     .
     *
     * @param {HTMLInputElement} passwordInput  .
     * @returns {boolean}   .
     */
    static isValidPasswords(passwordInput) {
        const passwordGroup = passwordInput.closest(".js-password-group");

        if (!passwordGroup) {
            return !!passwordInput.value;
        } else {
            const inputs = passwordGroup.querySelectorAll("input");
            //         
            if(inputs[0].value && inputs[1].value) {
                const passwordGroupError = passwordInput.closest(".js-password-group").querySelector(".js-caption-error");
                if(!(inputs[0].value === inputs[1].value) && passwordGroupError) {
                    passwordGroupError.innerHTML = "";
                    passwordGroupError.innerHTML = "  ";
                } else {
                    passwordGroupError.innerHTML = "";
                }
                return inputs[0].value === inputs[1].value;
            } else {
                return true
            }
        }
    }

    /**
     *     .  .
     *
     * @param {HTMLInputElement} numberInput   .
     * @returns {boolean}   .
     */
    static isValidDate(dateInput) {

        const parentNode = dateInput.closest(".js-form-field");
        const errorField = parentNode.querySelector(".js-form-field-caption");

        if(dateInput.value.length < 10) {
            parentNode.classList.add("with-error");
            errorField.innerHTML = "    ..";
            return false;
        } else {
            let userDate = (dateInput.value);
            let userModifiedDate = userDate.replace(/(\d+).(\d+).(\d+)/, "$3/$2/$1");
            let userFormattedDate = new Date(userModifiedDate);
            let currentDate = new Date();

            if(userFormattedDate > currentDate) {
                parentNode.classList.add("with-error");
                errorField.innerHTML = "   ";
                return false;
            } else {
                parentNode.classList.remove("with-error");
                errorField.innerHTML = "";
                return true;
            }
        }
    }
}
