/**    textarea    . */
export class TextareaCounter  {
    /**
     *  .
     *
     * @param {Element} el  .
     */
    constructor(el) {
        this.el = el;
        this.counterElement = this.el.closest(".js-form-field").querySelector(".js-textarea-counter");
        this.bindEventListeners();
    }
    /**
     *     textarea.
     *
     * @returns {void}
     */
    bindEventListeners() {
        this.el.addEventListener("input", this.updateCounter.bind(this));
    }

    /**
     *      .
     *
     * @returns {void}
     */
    updateCounter() {
        const currentCount = this.el.value.length;
        this.counterElement.innerHTML = currentCount;
    }
}


document.querySelectorAll(".js-form-field-with-counter").forEach((el) => new TextareaCounter (el));
