/**
 *      .
 *
 * @param {EventTarget} target ,   .
 * @param {string} className    .
 *
 * @returns {HTMLElement || null} btn    ,    .
 */
export const getBtnFromTarget = (target, className) => {
    let btn = null;
    if (target.classList.contains(className)) {
        btn = target;
    } else {
        btn = target.closest("." + className);
    }
    return btn;
}
